html,
	body,
	div.wrapper-to-position-in-the-middle {
		height: 100%;
		width: 100%;
	}

	/* center with flexbox */
	div.wrapper-to-position-in-the-middle {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		  -webkit-box-align: center;
		-webkit-align-items: center;
			 -ms-flex-align: center;
				align-items: center;
			   -webkit-box-pack: center;
		-webkit-justify-content: center;
				  -ms-flex-pack: center;
				justify-content: center
	}

	/*
	 * limit width to fit the video in the available height
	 * must limit width because the video "height" is padding, not height
	 */
	div.wrapper-with-inverse-ratio-16x9 {
		-webkit-box-flex: 1 0 100%;
			-webkit-flex: 1 0 100%;
				-ms-flex: 1 0 100%;
					flex: 1 0 100%;
		max-width: 177.778vh;
		max-width: calc(16 / 9 * 100vh);
	}

	/*
	 * maintain aspect ratio by using padding instead of height
	 */
	div.wrapper-with-intrinsic-ratio-16x9 {
		height: 0;
		overflow: hidden;
		padding-bottom: 56.25%;
		padding-bottom: calc(9 / 16 * 100%);
		position: relative;
	}

	/* stretch the iframe to fill it's parent element */
	iframe.element-to-stretch {
		height: 100%;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%;
	}
